.introjs-overlay {
  position: absolute;
  z-index: 999999;
  opacity: 0;
  transition: all 0.3s ease-out;
}
.introjs-fixParent {
  z-index: auto !important;
  opacity: 1.0 !important;
}
.introjs-showElement {
  z-index: 99999999 !important;
}
.introjs-relativePosition {
  position: relative;
}
.introjs-helperLayer {
  position: absolute;
  z-index: 9999998;
  background-color: transparent;
  border: 1px solid rgba(234, 198, 28, 0.5);
  border-radius: 0;
  box-shadow: 0 5px 8px -3px rgba(0, 0, 0, 0.6);
  transition: all 0.3s ease-out;
}
.introjs-helperNumberLayer {
  position: absolute;
  top: 100%;
  left: 5px;
  z-index: 9999999999 !important;
  padding: 2px;
  font-family: Arial, verdana, tahoma;
  font-size: 19px;
  font-weight: bold;
  color: #fff;
  /* Old browsers */
  /* Chrome10+,Safari5.1+ */
  background: #DA4433;
  width: 25px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.35);
  height: 26px;
  text-align: center;
  line-height: 20px;
  border: 2px solid #DA4433;
  border-radius: 0;
  /* IE6-9 */
  /* IE10 text shadows */
}
.introjs-arrow {
  border: 5px solid white;
  content: '';
  position: absolute;
}
.introjs-arrow.top {
  top: -10px;
  border-top-color: transparent;
  border-right-color: transparent;
  border-bottom-color: #ecf0f1;
  border-left-color: transparent;
}
.introjs-arrow.right {
  right: -10px;
  top: 10px;
  border-top-color: transparent;
  border-right-color: transparent;
  border-bottom-color: transparent;
  border-left-color: #ecf0f1;
}
.introjs-arrow.bottom {
  bottom: -10px;
  border-top-color: #ecf0f1;
  border-right-color: transparent;
  border-bottom-color: transparent;
  border-left-color: transparent;
}
.introjs-arrow.left {
  left: -10px;
  top: 10px;
  border-top-color: transparent;
  border-right-color: #ecf0f1;
  border-bottom-color: transparent;
  border-left-color: transparent;
}
.introjs-tooltip {
  position: absolute;
  padding: 10px 10px;
  background-color: #ecf0f1;
  min-width: 200px;
  max-width: 1000px;
  border-radius: 3px;
  /* border-radius: 3px; */
  /* box-shadow: 0 6px 7px -4px rgba(0,0,0,.4); */
  transition: opacity 0.1s ease-out;
  /* border: 5px double #0787AF; */
}
.introjs-tooltiptext {
  margin-left: -10px;
  margin-right: -10px;
  /* border-top: 1px solid #FFFFFF; */
  background: #FAFAFA;
  color: #2c3e50;
  padding: 25px 30px 15px;
  /* border-bottom: 1px solid #FFFFFF; */
  font-size: 1.1em;
}
.introjs-tooltiptext label {
  float: right;
  display: inline;
  width: 219px;
  font-size: 15px;
  color: #b17e7e;
}
.introjs-tooltipbuttons {
  text-align: center;
  /* background: rgba(0, 0, 0, 0.06); */
  border-radius: 0 0 8px 8px;
  padding-bottom: 10px;
  background: #EEEEEF;
}
/* 
 Buttons style by http://nicolasgallagher.com/lab/css3-github-buttons/ 
 Changed by Afshin Mehrabani
*/
.introjs-button {
  position: relative;
  overflow: visible;
  display: inline-block;
  padding: 0.5em 0.8em;
  /* box-shadow: 0 2px 0px -0px #306588; */
  text-shadow: none;
  margin: 0;
  border: 1px solid;
  background: transparent;
  text-decoration: none;
  font: 12px / normal sans-serif;
  color: #c9dbe6;
  white-space: nowrap;
  cursor: pointer;
  outline: none;
  -webkit-background-clip: padding;
  -moz-background-clip: padding;
  -o-background-clip: padding-box;
  /*background-clip: padding-box;*/
  /* commented out due to Opera 11.10 bug */
  border-radius: 0.7em;
  /* IE hacks */
  zoom: 1;
  *display: inline;
  margin-top: 10px;
  transition: all 0.25s ease;
  -webkit-transition: all 0.25s ease;
  -moz-transition: all 0.25s ease;
  -ms-transition: all 0.25s ease;
  -o-transition: all 0.25s ease;
}
.introjs-button:hover {
  color: #fff;
  background: #2671A2;
  text-decoration: none;
  border-color: #235677;
}
.introjs-button:focus,
.introjs-button:active {
  background: #23587A;
  text-decoration: none;
  color: #fff;
  /* bottom: -1px; */
  box-shadow: none;
  border-color: #173B53;
}
/* overrides extra padding on button elements in Firefox */
.introjs-button::-moz-focus-inner {
  padding: 0;
  border: 0;
}
.introjs-skipbutton {
  margin-right: 25px;
  color: #ffffff;
  background: #717f86;
  border-color: #eeeeee;
  font-size: 13px !important;
  font-weight: normal;
  border-radius: 0.7em;
}
.introjs-skipbutton:hover {
  background: #229122;
  border-color: #eeeeee;
  font-size: 14px !important;
}
.introjs-skipbutton:active,
.introjs-skipbutton:focus {
  background: #12c033;
  border: none;
  box-shadow: 0 1px 0px 0px rgba(69, 69, 69, 0.49);
}
.introjs-prevbutton {
  border-radius: 0.7em 0 0 0.7em;
  border-right: none;
  color: gray;
}
.introjs-nextbutton {
  border-radius: 0 0.7em 0.7em 0;
}
.introjs-disabled,
.introjs-disabled:hover,
.introjs-disabled:focus {
  color: #C2C2C2 !important;
  border-color: #d4d4d4;
  cursor: default;
  /* box-shadow: 0 2px 0px -0px #CACED1; */
  background-color: #E6E6E6;
  background-image: none;
  text-decoration: none;
}
.introjs-bullets {
  text-align: center;
  position: absolute;
  left: 0;
  right: 0;
  top: -5px;
  background: #eeeeee;
}
.introjs-bullets ul {
  clear: both;
  margin: 15px auto 0;
  padding: 0;
  display: inline-block;
}
.introjs-bullets ul li {
  list-style: none;
  float: left;
  margin: 0 3px;
}
.introjs-bullets ul li a {
  display: block;
  width: 6px;
  height: 6px;
  background: #ccc;
  border-radius: 10px;
  -moz-border-radius: 10px;
  -webkit-border-radius: 10px;
  text-decoration: none;
}
.introjs-bullets ul li a:hover {
  background: #999;
}
.introjs-bullets ul li a.active {
  background: #CABFBF;
  width: 12px;
  height: 12px;
  margin-top: -3px;
}
.introjsFloatingElement {
  position: absolute;
  height: 0;
  width: 0;
  left: 50%;
  top: 50%;
}
label .required {
